import React from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

// Registrando os componentes do Chart.js
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

const Dashboard = () => {
  // Dados do gráfico
  const data = {
    labels: ["Jan", "Fev", "Mar", "Abr", "Mai", "Jun"], // Meses
    datasets: [
      {
        label: "Vendas",
        data: [10, 20, 30, 25, 40, 50], // Valores das vendas
        borderColor: "rgba(75,192,192,1)", // Cor da linha
        backgroundColor: "rgba(75,192,192,0.2)", // Cor do fundo da linha
        fill: true, // Preenchendo abaixo da linha
      },
    ],
  };

  // Opções do gráfico
  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top" as const,
      },
    },
  };

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Dashboard</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="bg-blue-100 shadow-lg rounded p-4">
          Produtos Cadastrados: 100
        </div>
        <div className="bg-yellow-100 shadow-lg rounded p-4">
          Produtos em Baixa: 10
        </div>
        <div className="bg-green-100 shadow-lg rounded p-4">
          Produtos Vendidos: 50
        </div>
      </div>
      <div className="bg-white shadow-lg rounded p-6 mt-6">
        <h2 className="text-xl font-bold mb-4">Gráfico de Vendas</h2>
        <Line data={data} options={options} />
      </div>
    </div>
  );
};

export default Dashboard;
